const serverApiRequest = async a => {
  /*simulate request*/
  try {
    const response = await fetch("//t.syshub.ru" + a);
    if (!response.ok) {
      throw new Error("Request error(error code: " + response.status);
    }
    const data = await response.json();
    return data;
  } catch (e) {
    return e.name + ": " + e.message;
  }
};

